.version-label {
    position: absolute;
    bottom: 0px;
    left: 5px;
    font-size: .8em;
    opacity: 0.5;
    z-index: 1000;
    color: var(--ion-color-medium);
}
  
.dashboard {
    height: 100%;
    &-header {
        &-toolbar {
            ion-icon {
                height: 40px;
                width: 50px;
            }
            &-battery-icon {
                height: 100%;
                width: 50px;
                text-align: center;
                .battery {
                    &-container {
                        fill: var(--ion-color-primary-contrast);
                    }
                    &-gauge {
                        fill: var(--ion-color-primary-tint);
                    }
                    &-button {
                        fill: #ccc;
                    }
                    &-text {
                        fill: var(--ion-color-medium-shade);
                        font-size: 11px;
                    }
                }
            }
            &-logo {
                height: 40px;
                margin: 10px;
            }
        }
    }
    &-content {
        &-list {
            height: 100%;
            display: flex;
            flex-direction: column;
            align-content: center;
            &-spacer {
                flex: 1;
            }
            &-footer {
                margin-bottom: 20px;
            }
            &-header {
                color: var(--ion-color-medium);
                padding: 0px;
                ion-label {
                    margin: auto;
                }
            }
            &-item {
                height: 100%;
                display: flex;
                .swiper-container {
                    height: 100%;
                }
                .swiper-slide {
                    display: flex;
                    flex-direction: column;
                    $slide-size-base: 125px;
                    &-content {
                        display: flex;
                        height: $slide-size-base;
                        width: $slide-size-base;
                        @media screen and (min-width: 350px) and (min-height: 450px) {
                            height: $slide-size-base * 1.5;
                            width: $slide-size-base * 1.5;
                        }
                        @media screen and (min-width: 600px) and (min-height: 600px) {
                            height: $slide-size-base * 2;
                            width: $slide-size-base * 2;
                        }
                        @media screen and (min-width: 750px) and (min-height: 750px) {
                            height: $slide-size-base * 2.5;
                            width: $slide-size-base * 2.5;
                        }
                        @media screen and (min-width: 1000px) and (min-height: 1000px) {
                            height: $slide-size-base * 3;
                            width: $slide-size-base * 3;
                        }
                        border-radius: 50%;
                        background-color: var(--ion-color-primary);
                        border-color: var(--ion-color-medium-light);
                        border-width: 3px;
                        border-style: groove;
                        color: var(--ion-color-primary-contrast);    
                        margin: auto;
                        &-text {
                            font-size: 1.5em;
                            @media screen and (min-width: 450px) and (min-height: 450px) {
                                font-size: 2em;
                            }
                            margin: auto;
                        }
                    }
                    &-footer {
                        color: var(--ion-color-medium);    
                        margin: auto;
                    }
                    &-temp {
                        width: 80%;
                        margin-bottom: 60px;
                        z-index: 9999;
                        user-select: none;
                    }
                }
            }
        }
    }
}