.version-label {
    position: absolute;
    bottom: 0px;
    left: 5px;
    font-size: .8em;
    opacity: 0.5;
    z-index: 1000;
    color: var(--ion-color-medium);
}
  
.connect {
    height: 100%;
    &-header {
        &-toolbar {
            &-logo {
                height: 40px;
                margin: 10px;
            }
        }
    }
    &-content {
        &-list {
            height: 100%;
            display: flex;
            flex-direction: column;
            align-content: center;
            &-spacer {
                flex: 1;
            }
            &-header {
                color: var(--ion-color-medium);
                padding: 0px;
                ion-label {
                    margin: auto;
                }
            }
            &-button {
                display: flex;
                margin: auto;
            }
            &-options {
                display: flex;
                margin: auto;
            }
        }
    }
}